.order {
	background: #ccc;
	-ms-flex: 1 1 0%;
	    flex: 1 1 0%;
	overflow-y: scroll;
}
.order-list {
	list-style: none;
	text-align: left;
}
.order-list li > span {
  position: relative;
}
.order-list li button {
  font-size: 22px;
  border: 0;
  background: transparent;
  display: none;
  line-height: 1;
  padding: 0;
  cursor: pointer;
}
.order-list li:hover button {
  display: inline;
  background: #000;
  color: #fff;
}
button.print-button, button.remove-button {
	border: none;
	background: transparent;
	cursor: pointer;
} 
button.print-button:focus, button.remove-button:focus {
	outline: none;
	outline-style: none;
	outline-width: 0;
}
button.print-button::-moz-focus-inner, button.remove-button::-moz-focus-inner {
	border: 0;
}
button.print-button:hover, button.remove-button:hover {
	color: #000;
}

/* ANIMATION CSS */

.order-enter {
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translateX(-120%);
      -ms-transform: translateX(-120%);
          transform: translateX(-120%);
  max-height: 0;
}

.order-enter.order-enter-active {
  max-height: 50px;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.order-leave {
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  max-height: 50px;
}

.order-leave.order-leave-active {
  -webkit-transform: translateX(120%);
      -ms-transform: translateX(120%);
          transform: translateX(120%);
  max-height: 0;
  padding: 0;
}

.count-enter {
  -webkit-transition: all 0.25s;
  -o-transition: all 0.25s;
  transition: all 0.25s;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
}

.count-enter.count-enter-active {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.count-leave {
  -webkit-transition: all 0.25s;
  -o-transition: all 0.25s;
  transition: all 0.25s;
  position: absolute;
  left: 0px;
  bottom: 0px;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.count-leave.count-leave-active {
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}


